import React, { PureComponent } from 'react'
import { Helmet } from 'react-helmet'
import { init, version } from 'klinecharts'
import FileSaver from 'file-saver'
import SettingList from './settinglist/SettingList'

import set from 'lodash/set'

import './kline.less'

class KLine extends PureComponent {
  constructor (props) {
    super(props)
    this.throttle = (func, wait) => {
      let previous = 0
      return () => {
        const now = Date.now()
        if (now - previous > wait) {
          func()
          previous = now
        }
      }
    }
    this.onResize = this.throttle(() => {
      if (this.chart) {
        this.chart.resize()
      }
    }, 1000 / 16)
    this.chart = null
    this.state = {
      isLoading: false
    }
  }

  componentDidMount () {
    this.chart = init(document.getElementById('k_chart'))
    const defaultDataList = this.generatedKLineDataList()
    this.chart.addData(defaultDataList)
    this.chart.loadMore(() => {
      this.setState({ isLoading: true })
      setTimeout(() => {
        this.setState({ isLoading: false })
        this.chart.addData(this.generatedKLineDataList(200, defaultDataList[0].close), 0)
      }, 2000)
    })
    window.addEventListener('resize', this.onResize, false)
  }

  componentWillUnmount () {
    window.removeEventListener('resize', this.onResize, false)
  }

  /**
   * 生成k线数据
   * @returns {Array}
   */
  generatedKLineDataList (dataSize = 500, basePrice = Math.random() * 12000) {
    const dataList = []
    let timestamp = new Date().getTime() - 12 * 24 * 3600
    let baseValue = basePrice
    const prices = []
    for (let i = 0; i < dataSize; i++) {
      baseValue = baseValue + Math.random() * 20 - 10
      for (let j = 0; j < 4; j++) {
        prices[j] = (Math.random() - 0.5) * 12 + baseValue
      }
      prices.sort()
      const openIdx = +Math.round(Math.random() * 3).toFixed(0)
      let closeIdx = +Math.round(Math.random() * 2).toFixed(0)
      if (closeIdx === openIdx) {
        closeIdx++
      }
      const volume = Math.random() * 100

      timestamp += 60 * 1000

      const kLineModel = {
        open: prices[openIdx],
        low: prices[0],
        high: prices[3],
        close: prices[closeIdx],
        volume: volume,
        timestamp,
        turnover: prices[closeIdx] * volume
      }
      if (basePrice) {
        dataList.unshift(kLineModel)
      } else {
        dataList.push(kLineModel)
      }
    }
    return dataList
  }

  /**
   * 配置变化事件
   * @param key
   * @param value
   */
  onStyleChange = (key, value) => {
    if (this.chart) {
      const c = set(this.chart.getStyle(), key, value)
      this.chart.setStyle(c)
    }
  }

  /**
   * 特殊配置变化事件
   * @param specialType
   * @param value
   */
  onSpecialConfigChange = (specialType, value) => {
    switch (specialType) {
      case 'export': {
        const file = new File([JSON.stringify(this.chart.getStyle())], 'style.json', { type: 'text/plain;charset=utf-8' })
        FileSaver.saveAs(file)
        break
      }
      case 'addData': {
        const dataList = this.chart.getDataList()
        const basePrice = (dataList[dataList.length - 1] || {}).close
        const kLineData = this.generatedKLineDataList(1, basePrice)[0]
        this.chart.addData(kLineData)
        break
      }
      case 'picture': {
        const url = this.chart.getConvertPictureUrl()
        const a = document.createElement('a')
        a.download = ''
        a.href = url
        document.body.appendChild(a)
        a.click()
        a.remove()
        break
      }
      case 'defaultRange': {
        this.chart.setDefaultRange(value)
        break
      }
      case 'minRange': {
        this.chart.setMinRange(value)
        break
      }
      case 'maxRange': {
        this.chart.setMaxRange(value)
        break
      }
      case 'volChart': {
        this.chart.showVolChart(value)
        break
      }
      case 'mainIndicator': {
        this.chart.setMainIndicatorType(value)
        break
      }
      case 'subIndicator': {
        this.chart.setSubIndicatorType(value)
        break
      }
      case 'drawMarker': {
        this.chart.drawMarker(value.key)
        break
      }
      case 'clearAllMarker': {
        this.chart.clearAllMarker()
        break
      }
      case 'chartType': {
        this.chart.setMainChartType(value)
        break
      }
    }
  }

  onIndicatorParamsChange = (indicatorType, values) => {
    this.chart.setIndicatorParams(indicatorType, values)
  }

  render () {
    return (
      <div className="kline">
        <Helmet>
          <title>k线图(K Stock Chart)</title>
          <meta name="keywords" content="kline,stocks,candlestick,time-line,tradingview,canvas,charts"/>
          <meta name="description" content="k线图demo(K stock chart demo)" />
        </Helmet>
        <div className="kline-setting-container">
          <div className="kline-setting-title-container">
            <span>
              K线图
              <span className="kline-setting-title-version">&nbsp;(v{version})</span>
            </span>

            <a
              className="kline-setting-title-link"
              target="_blank"
              rel="noopener noreferrer nofollow"
              href="https://github.com/liihuu/klineweb">
              <i className="icon-github kline-setting-title-link-icon"/>
              <span>Star</span>
            </a>
          </div>
          <SettingList
            onStyleChange={this.onStyleChange}
            onSpecialConfigChange={this.onSpecialConfigChange}
            onIndicatorParamsChange={this.onIndicatorParamsChange}/>
        </div>
        <div className="kline-chart-container">
          <div
            id="k_chart"
            className="kline-chart"/>
          {
            this.state.isLoading &&
            <div className="kline-chart-loading-container">
              <i className="kline-chart-loading"/>
              <span className="kline-chart-loading-text">正在加载</span>
            </div>
          }
        </div>
      </div>
    )
  }
}

export default KLine
